
var login={
      methods:{
          init(){
             $(".username").on("change",login.events.accountCover);
             $(".login").on("click",login.events.land)
          }
      },
      events:{
        accountCover(){
            var username=$(".username").val();
            $.defs.loadingshow();
            $.defs.myPost({
                m:"multi_result",
                SP_NAME:"SP_USER_LOGIN_BEF_SELECT",
                MEME_LOGIN:username
            },"../api/preLogin").then((data)=>{
                $.defs.loadinghide();
                let arr=data;
                if(arr.RETURN_CODE==0){
                   var array=arr.result.group_account;
                   if(array.length>1){
                       array.unshift({
                           GPGP_KY:0,
                           GPGP_NAME:"选择公司账套"
                       })
                   }
                 var optionHtml=$.defs.selectOption(array,"GPGP_KY","GPGP_NAME");
                   $(".weui-select").html(optionHtml)
               }else{
                    $.defs.msg(arr.RETURN_MESSAGE)
               }
            })

        },
        land(){
            var username=$(".username").val();
            var password=$("input[type='password']").val();
            var select=$(".weui-select").val();
            if(username==""||password==""||select==""){
                $.defs.msg("请将员工号、密码、公司账套填完整")
            }else{
                $.defs.loadingshow("正在登陆中。。。")
                $.defs.ajax({
                    url:"../api/login",
                    data:{
                        MEME_LOGIN:username,
                        MEME_PASSWORD:password,
                        GPGP_KY:select
                    },
                    success:function(data){
                        $.defs.loadinghide()
                        if(data.RETURN_CODE==0){
                            localStorage.setItem("EHUSER",data.EHUSER);
                            window.location.replace("../index/index")
                        }else{
                          $.defs.msg(data.RETURN_MESSAGE)
                        }
                    }
                })
            }
        }
      },
      config:{
          init(){
              localStorage.removeItem("status");
            $("input[type='password']").keydown((ev)=>{
                let e=ev||window.ev;
                if(e.keyCode==13){
                    login.events.land()
                }
            })
          }
      }
}
$(function(){
    login.config.init();
    login.methods.init();
})


